@import url('https://fonts.googleapis.com/css?family=Nunito&display=swap');
@import url('https://fonts.googleapis.com/css?family=Permanent+Marker&display=swap');
@import '~bootstrap/scss/bootstrap';
@import './mixins.scss';

$font-stack: 'Nunito', sans-serif;
$font-marker: 'Permanent Marker', cursive;
$active-primary: #a2b73c;
$active-primary-hover: #293638;

html {
  font-size: 62.5%;
}

body {
  font: 100% $font-stack;
  font-size: 1.6rem;
  position: relative;
}

head,
body {
  height: 100%;
}

// .row {
//   margin-right: 0;
//   margin-left: 0;
// }

@include media-breakpoint-down(sm) {
  body {
    font-size: 1.2rem;
  }
  .rich-text p {
    font-size: 1.5rem;
  }
}

@include media-breakpoint-up(md) {
  body {
    font-size: 1.4rem;
  }
  .rich-text p {
    font-size: 1.7rem;
  }
}

@include media-breakpoint-up(lg) {
  body {
    font-size: 1.6rem;
  }
  .rich-text p {
    font-size: 1.9rem;
  }
}

#app {
  position: relative;
  min-height: 100vh;
}

img {
  height: 100%;
  width: 100%;
  object-fit: contain;
}

.mail {
  color: $active-primary;
}
.mail:hover {
  color: $active-primary-hover;
}

p {
  line-height: 1.6;
}
.rich-text {
  h1 {
    line-height: 1.6;
  }
  .green-header {
    color: $active-primary;
    font-weight: bold;
    text-align: center;
  }
  span.green {
    font-weight: bold;
  }
}

.blockquote-footer {
  color: $active-primary;
  font-size: calc(1rem + 0.6vw);
}

#top {
  position: fixed;
  bottom: 90px;
  right: 50px;
  color: #fff;
  background-color: $active-primary;
  z-index: 10;
  width: 50px;
  height: 50px;
  border-radius: 5px;
  animation: top-animation 1s;
}
#top:hover {
  background-color: $active-primary-hover;
  color: #fff;
  cursor: pointer;
}

@keyframes top-animation {
  0% {
    bottom: 0px;
  }
  100% {
    bottom: 90px;
  }
}

// HEADER
.header {
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(36, 36, 36, 0.4);
  z-index: 10;
  @include transition(0.2s ease-in);
  overflow: hidden;
}

.active-header {
  background-color: #a2b73c;
  @include transition(background-color 0.4s ease-out);
}

// .navbar-expand-lg .navbar-collapse {
//   margin-bottom: 20px;
// }

.logo-container {
  width: 300px;
  margin: 30px auto;
  .logo {
    background-image: url('../images/leserglck_wei.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    height: 100px;
    width: 100%;
  }
}

.navbar-dark .navbar-toggler {
  border: none;
}

.navbar-header .icon-menu::before {
  top: -10px;
}
.navbar-header .icon-menu::after {
  top: 10px;
}

.navbar-header .icon-menu.active::before {
  @include transform(rotate(45deg));
}

.navbar-header .icon-menu.active::after {
  @include transform(rotate(-45deg));
}

.navbar-header .icon-menu.active::before,
.navbar-header .icon-menu.active::after {
  top: 0;
}

.navbar-header .icon-menu {
  background: #fff;
  display: inline-block;
  height: 4px;
  position: relative;
  width: 33px;
  cursor: pointer;
  @include transition(all, 0.1s, linear);
}

.navbar-header .icon-menu::before,
.navbar-header .icon-menu::after {
  background: #fff;
  content: '';
  display: inline-block;
  height: 4px;
  right: 0;
  position: absolute;
  @include transition(all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275));
  @include transform-origin(50% 50%);
  width: 33px;
}

.navbar-header .icon-menu.active {
  background: transparent;
}

.icon-menu::before {
  content: '\e90c';
}

@include media-breakpoint-down(sm) {
  .logo-container {
    width: 250px;
    margin: 30px auto 0 auto;
  }
  #services .col-sm {
    margin-top: 2rem;
    margin-bottom: 2rem;
  }
}

@include media-breakpoint-up(md) {
  .navbar {
    display: block;
  }
}
@include media-breakpoint-down(md) {
  .navbar {
    display: flex;
    width: 100%;
  }
}

.main {
  .nav-item {
    margin: 0 1rem;
    .nav-link {
      font-size: 1.6rem;
      text-transform: uppercase;
      color: white;
      padding: 5px;
    }
    .active {
      background: $active-primary;
      border-radius: 5px;
    }
  }
}

.navbar-toggler {
  margin: 15px auto;
}

.hero {
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  position: relative; /* Useful for any absolute positioned elements inside */

  /* Sizing */
  //width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero-text {
  color: white;
  h1,
  button {
    margin-bottom: 1rem !important;
    margin-top: 1rem !important;
  }
}

.action-btn-primary {
  border: none;
  border-radius: 5px;
  outline: 0;
  display: inline-block;
  padding: 10px 25px;
  color: #fff;
  background-color: $active-primary;
  text-align: center;
  text-transform: capitalize;
  cursor: pointer;
  font-weight: bold;
}

.action-btn-primary:hover {
  background-color: $active-primary-hover;
  color: white;
}

.hero-home {
  background: url(../images/hero-bg.jpg) no-repeat center;
  background-size: cover;
}

.marker {
  font-family: $font-marker;
}

/*Second Section*/
